@import "~Styles/colors.module";
@import "~Styles/mixins.module";

.flex_2 {
  flex: 2;
}

.flex_1 {
  flex: 1;
}

.coupon_header_text {
  font-size: 12px;
  font-weight: bold;
  color: white;
  margin: 24px 0px 10px;
}

.checkout_container {
  padding: 20px 60px 0px;
  overflow: scroll;

  .cart_list_container {
    flex: 1;
    padding-right: 50px;
    overflow: scroll;

    .table_header {
      padding: 8px;
      font-size: 12px;
      font-weight: bold;
      line-height: 2;
      color: $primary-text-color;
      border-bottom: 1px solid #ededed;
    }

    .table_content_container {
      margin: 15px; // overflow: scroll;
    }
  }

  .price_details_container {
    padding: 6px 0px;

    .title {
      font-size: 12px;
      font-weight: 300;
      color: white;
      opacity: 0.8;
    }

    .value {
      font-size: 12px;
      font-weight: 300;
      color: white;
      opacity: 0.8;
      margin-left: auto;
    }
  }

  .price_divider {
    margin-top: 12px;
  }

  .capsule_button {
    padding: 12px 0px;
    align-self: stretch;
    font-size: 18px;
    font-weight: 900;
    color: white;
    text-transform: capitalize;
    margin-top: 19px;
  }
}

.empty_page_container {
  margin-top: 24px;
}

.header_container {
  border-top: 0.5px solid #ededed;
  border-bottom: 0.5px solid #ededed;
  padding: 11px 33px;
  justify-content: space-between;

  .header_title {
    text-transform: uppercase;
    font-size: 12px;
    color: $primary-text-color;
    align-self: center;
  }

  .header_button {
    font-size: 10px;
    color: white;
    border-radius: 50px;
    padding: 6px 20px;
    background-color: $capsule-button-color;
  }
}

.address_item {
  width: 426px;
  min-height: 164px;
  margin: 8px 0px 0px 33px;
  border: solid 0.5px rgba($primary-text-color, 0.1);
  background-color: rgba($color-very-light-blue, 0.9);
  cursor: pointer;

  .item_content_container {
    padding: 18px 16px 0px 16px;

    .item_name {
      font-size: 14px;
      font-weight: bold;
      color: $primary-text-color;
    }

    .item_address {
      font-size: 12px;
      font-weight: 300;
      color: $primary-text-color;
      margin: 8px 0px 11px;
    }

    .item_phonenumber {
      font-size: 12px;
      font-weight: 300;
      color: $primary-text-color;
    }
  }

  .action_container {
    border: solid 1px rgba($primary-text-color, 0.1);

    .action_button {
      padding: 11px 0px;
      text-transform: uppercase;
      flex: 1;
      text-align: center;
      font-size: 12px;
      color: $color-orangey-red;
    }
  }
}

.selected_address {
  border: 2px solid $color-gold;
}

.selected_delivery {
  border: solid 1px white;
}


.payment_method_container {
  padding: 20px 10px;

}

.payment_method_item_container {
  padding: 10px 16px;
  border: 1px solid $primary-text-color;
  max-width: 400px;
  @include on_click_button;

  .title {
    font-size: 14px;
    color: $primary-text-color;
    font-weight: bold;
    margin-bottom: 6px;
  }

  .description {
    font-size: 10px;
    color: $primary-text-color;    
  }

  .icon {
    width: 24px;
  }

}
